@media only screen and (max-width: 1366px) {
  .title--underground {
    font-size: 8rem;
  }
  .about-text {
    z-index: 2;
    position: relative;
  }
  .about-img--big {
    top: 100px;
    width: 53%;
    height: 350px;
    background-size: cover;
  }
  .about-block--gray {
    background-size: 40% auto;
    background-position: 40px 40px;
  }
}

@media screen and (max-width: 1279px) {
  .gcellImg img.js-animation {
    display: none;
  }
  .screen6 {
    padding: 100px 0;
  }
}

@media only screen and (max-width: 1024px) {
  .notebook img {
    width: 100%;
  }
  .notebook-screen {
    top: 17.1%;
    left: 15.1%;
    width: 69.7%;
    height: 63.6%;
  }
  .screen-changer {
    margin-bottom: 30px;
  }
  .monitors {
    display: none;
  }
  .left-size {
    max-width: 100%;
    width: 100%;
  }
  .screen1 {
    height: auto;
    padding: 30px 0;
  }
  .screen1-slogan {
    max-width: 100%;
  }
  .screen2,
  .screen3,
  .screen4,
  .screen6 {
    padding: 30px 0;
  }
  .screen5 {
    padding-top: 30px;
  }
  .about {
    margin-bottom: 30px;
  }
  .title--underground {
    font-size: 6rem;
  }
  .price {
    margin-bottom: 60px;
  }
  .order-opus {
    line-height: 0;
    padding: 30px 0;
  }
  .order-opus__inn {
    max-width: 100%;
    text-align: center;
    line-height: 0;
    height: auto;
  }
  .order-form {
    margin-top: 0;
    margin-bottom: 30px;
    padding: 5% 7%;
  }
  .screen6 .gcell:first-child {
    margin-bottom: 30px;
  }
  .screen6 .gcell img {
    display: none;
  }
  .may-hide {
    display: none !important;
  }
  .comment-text:after {
    display: none;
  }
  .about-img--big {
    background-size: 100% auto;
    right: -18px;
  }
}

@media screen and (max-width: 860px) {
  .js-anchor-block3 .gcell--5, .js-anchor-block3 .gcell--7 {
    flex-basis: 100%;
    max-width: 100%;
    width: 100%;
  }
}

@media only screen and (max-width: 768px) {
  .footer-top {
    height: auto;
    line-height: 0;
    padding: 30px 0;
  }
  .view-size .view-size {
    padding: 0;
  }
  .title--underground {
    font-size: 4rem;
    line-height: 6rem;
  }
  .about-block--gray {
    background-image: none;
    text-align: center;
  }
  .about-block--gray .about-text {
    margin-right: 0;
  }
  .about-img--big {
    display: none;
  }
  .about-block--fiol {
    text-align: center;
  }
  .about-block--fiol .about-text {
    margin-left: 0;
  }
}

@media only screen and (max-width: 736px) {
  .price {
    height: 150px;
    line-height: 150px;
  }
  .price-center {
    height: 210px;
    line-height: 210px;
    width: 35%;
  }
}

@media screen and (max-width: 580px) {
  .about-left {
    font-size: 16px;
    line-height: 20px;
  }
  .about-right--title, .order-text, .order-slogan {
    font-size: 14px;
    line-height: 18px;
  }
}

@media only screen and (max-width: 414px) {
  .about-text-list ul,
  .columns {
    columns: 1;
  }
  .about-list__svg {
    width: 15%;
  }
  .about-list__svg svg {
    width: 25px;
    height: 25px;
  }
  .about-text-list ul li {
    padding-left: 20px;
    margin-bottom: 10px;
  }
  .about-text-list ul li:after {
    width: 15px;
    height: 8px;
    background-size: cover;
    top: 3px;
  }
  .about-list__item,
  .about-list {
    margin-bottom: 10px;
  }
  .comment-block {
    height: auto;
  }
  .comment-left {
    line-height: 200px;
    display: none;
  }
  .comment-right {
    line-height: 0;
    padding: 20px 0;
    box-sizing: border-box;
    float: none;
    width: 100%;
    max-width: 100%;
  }
  .notebook {
    display: none;
  }
  .screen-link span:after {
    display: none;
  }
  .screen-link.cur span:after {
    display: none;
  }
  .screen-link span:hover:after {
    display: none;
  }
  .screen-link span:hover {
    background: none;
    box-shadow: none;
    color: #fff;
  }
  .screen-link.cur span {
    background: none;
    box-shadow: none;
    color: #fff;
  }
  .screen-link {
    cursor: default;
  }
  .seo-block {
    margin-bottom: 0;
  }
  .screen-link {
    padding: 5px 0;
  }
  .screen-link.cur {
    background: none;
    color: #fff;
    box-shadow: none;
  }
  .screen-link:after {
    display: none;
  }
  .block-with-underground {
    padding-top: 40px;
  }
  .title--underground {
    font-size: 3rem;
    line-height: 4rem;
  }
  .screen1-title {
    font-size: 3rem;
    line-height: 4rem;
  }
  .logo {
    padding-bottom: 30px;
  }
  .about-block {
    height: 150px;
    line-height: 150px;
    margin-bottom: 10px;
  }
  .about-block--big {
    line-height: 350px;
    height: 350px;
  }
  .about-block--hulf + .about-block--hulf {
    margin-top: 10px;
  }
  .price-left span span,
  .price-right span span {
    font-size: 4rem;
    line-height: 3rem;
  }
  .price-left span span:after {
    font-size: 1.5rem;
    left: -10px;
  }
  .price-center {
    width: 45%;
  }
  .price-center span span {
    font-size: 8rem;
    line-height: 7rem;
  }
  .order-slogan {
    margin-bottom: 20px;
  }
  .screen1 .button,
  .screen7 .button {
    width: 100%;
  }
  .portfolio-link span {
    text-align: center;
    width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 90%;
    overflow: hidden;
  }
  .portfolio .gcell {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 374px) {
  .add-new {
    height: 0;
    padding-top: 68%;
    width: 100%;
    position: relative;
  }
}
